//Primary style=================================
body {
    min-height: 100%;
    
    color: #000;
    font-family: $fontFamily;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
}

footer {
    flex-shrink: 0;
}
//Primary style=================================

//Modules=======================================
.section-title {
    @include font-size(64px);
    font-weight: 600;
    line-height: 81px;
}

//Modules=======================================

//Wrapper=======================================
.wrapper {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
}
//Wrapper=======================================

//Main Block===================================
.page {
    flex: 1 0 auto;
}
//Main Block===================================

//Main Grid==================================
.container {
    max-width: $maxWidthContainer + px;
    margin: 0 auto;
    padding: 0 15px;
