.education-flow {

    padding: 74px 0 109px;

    &__inner {
    }

    &__title {
        margin: 0px 0px 39px 0px;
    }

    &__content {
        margin: 0px 0px 116px 0px;
        display: flex;
        justify-content: space-between;

        @media (max-width: 1200px){
            flex-direction: column-reverse;
        }
    }

    &__list {
        margin: 34px 0px 0px 0px;

        max-width: 414px;

        @media (max-width: 1200px){
            max-width: 100%;
        }
    }

    &__item {
        margin: 0px 0px 43px 0px;
        @include font-size(24px);
        line-height: 30.43px;

        &:last-child {
            margin: 0;
        }
    }

    &__img {
        margin-right: -15px;
        max-width: 745px;
        object-fit: cover;

        @media (max-width: 1200px){
            max-width: 100%;
        }
    }

    &__info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        overflow-x: auto;
        scroll-snap-type: x mandatory;

        @include font-size(24px);
        line-height: 30px;
        color: #fff;
        font-weight: 500;

        @media (max-width: 1200px){
            align-items: normal;
        }
    }

    &__hours {
        // padding: 34px 34px 29px 34px;
        padding: 34px;
        max-width: 597px;
        min-height: 287px;

        display: flex;
        flex-direction: column;

        background-color: #72E1B4;
        border-radius: 21px;

        @media (max-width: 1200px){
            margin: 0px 20px 0px 0px;
        }
    }

    &__hours-num {
        margin: 0px 0px 43px 0px;
    }

    &__hours-info {
    }

    &__projects {
        // padding: 0px 38px 0px 38px;
        padding: 33px 36px 32px 23px;

        max-width: 567px;
        min-height: 287px;

        display: flex;
        justify-content: space-between;
        flex: 1;
        align-items: center;

        background-color: #7E4FF6;
        border-radius: 21px;

        @media (max-width:$md2+px){
            flex-direction: column;
        }
    }

    &__projects-info {
    }

    &__projects-num {
        margin: 0px 0px 70px 0px;
    }

    &__projects-text {
    }
    &__projects-img {
        margin-bottom: 17px;

        @media (max-width:$md2+px){
            display: none;
        }
    }

    &__hours-num,
    &__projects-num {
        font-size: 134px;
        line-height: 0.8;
    }
}