.reviews {
    position: relative;

    padding: 0px 0px 167px 0px;

    &__title {
        margin: 0px 0px 68px 0px;
    }
}

.reviews-slider {

    &__item {
        display: flex;
        flex-direction: column;

        padding: 37px 41px 38px 46px;
        max-width: 589px;
        height: auto;

        border: 1px solid #D6D6D6;
        border-radius: 14px;

        @media (min-width:$md4+px){
            padding: 20px;
            height: 100%;
        }

        @media (min-width: 1024px){
            max-width: 100%;
        }
    }

    &__text {
        margin: 0px 0px 52px 0px;
        flex: 1;
    }

    &__author {
        display: flex;
    }

    &__avatar {
        margin: 0px 23px 0px 0px;
        width: 65px;
        height: 65px;
    }

    &__avatar-img {
        object-fit: cover;
        border-radius: 50%;
    }

    &__author-info {
        @include font-size(24px);
        line-height: 30px;
    }

    &__author-name {
    }

    &__author-position {
        color: #717171;
    }
}

// Slider pagination

.swiper-container.reviews-slider__container {
    position: unset;
}

.swiper-pagination.reviews-slider__pagination {
    bottom: 120px;
}

.swiper-pagination-bullet {
    width: 13px;
    height: 13px;
    background-color: #E3E3E3;
    opacity: 1;
}

.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0px 19px 0px 0px;
    &:last-child {
        margin: 0;
    }
}

.swiper-pagination-bullet-active {
    background-color: #7E4FF6;
}