.curriculum {

    padding: 0px 0px 122px 0px;

    &__inner {
        display: flex;
        justify-content: space-between;

        @media (max-width:$md2+px){
            flex-direction: column;
        }
    }

    &__title {
        margin: 0px 0px 56px 0px;
    }
}
.curriculum-accordion {

    margin: 0px 40px 0px 0px;
    width: 100%;
    max-width: 798px;

    @media (max-width:$md2+px){
        margin: 0px 0px 50px 0px;
    }

    @media (max-width:$md2+px){
        max-width: 100%;
    }

    &__item {
        border-top: 1px solid #D2D2D2;

        &:last-child {
            border-bottom: 1px solid #D2D2D2;
        }
    }

    &__item-header {
        padding: 28px 0 31px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        position: relative;
        cursor: pointer;
    }

    &__item-title {
        margin-right: auto;
        font-weight: 500;
    }

    &__item-title,
    &__list-header {
        @include font-size(24px);
        line-height: 30px;
    }

    &__avatars {
        margin-right: 90px;
        display: inline-flex;
        flex-direction: row-reverse;
    }

    &__avatars-img{
        position: relative;
        width: 60px;
        height: 60px;
        border-radius: 50%;
    }

    &__avatars-item:not(:last-child) {
        margin-left: -10px;
    }

    &__avatars-item:nth-child(1) {
        z-index: 3;
    }
    &__avatars-item:nth-child(2) {
        z-index: 2;
    }
    &__avatars-item:nth-child(3) {
        z-index: 1;
    }

    &__item-btn {
        margin: 0px 12px 0px 0px;
        width: 32px;
        height: 32px;

        background-color: #7E4FF6;
        border-radius: 30px;
        position: absolute;
        right: 0;

        &::after,
        &::before {
            content: "";
            position: absolute;
            width: 1px;
            height: 19px;
            background-color: #fff;
            border-radius: 30px;
        }

        &::after {
            transform: translate(-50%, -50%);
        }

        &::before {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(90deg); 
        }
    }

    &__item-content {
        max-width: 606px;
        overflow: hidden;

        height: 0;
        opacity: 0;
        visibility: hidden;
    }

    &__item-box {
        padding: 27px 0 53px;
    }

    &__list-header {
        margin: 0px 0px 16px 0px;
    }

    &__list {
        margin: 0px 0px 32px 0px;
    }

    &__list-item {
        margin: 0px 0px 23px 0px;
        padding-left: 12px;

        @include font-size(18px);
        line-height: 23px;
        position: relative;

        &:last-child {
            margin: 0;
        }

        &:before {
            content: "";
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: #7E4FF6;
            
            position: absolute;
            top: 45%;
            left: 2px;
        }
    }

    &__tools {
        display: flex;
        align-items: center;
    }

    &__tools-text{
        margin-right: 30px;
    }

    &__tools-items {
        display: flex;
        overflow-x: auto;
    }
    &__tools-item {
        margin-right: 15px;
        &:last-child {
            margin: 0;
        }
    }

    &__tools-img {
        width: 135px;
        object-fit: cover;
        max-width: unset;
    }
}

.curriculum-accordion__item.active .curriculum-accordion__item-content {
    height: auto;
    opacity: 1;
    visibility: visible;
}
.curriculum-accordion__item.active .curriculum-accordion__item-btn::after {
    display: none;
}

.curriculum-form {
    padding: 33px 27px 44px;

    max-width: 358px;
    max-height: 575px;

    background-color: #7E4FF6;
    border-radius: 18px;

    color: #fff;
    text-align: center;

    @media (max-width:$md2+px){
        max-width: 100%;
    }

    &__title {
        margin: 0px 0px 23px 0px;
        font-weight: 600;
        font-size: 36px;
        line-height: 38px;
    }

    &__text {
        margin: 0px 0px 28px 0px;
    }

    &__content {
        display: flex;
        flex-direction: column;
    }

    &__input {
        margin: 0px 0px 16px 0px;
        padding: 16px 16px 15px;
        width: 100%;

        background: #FFFFFF;
        border-radius: 8px;

        font-size: 24px;

        &::placeholder {
            color: rgba(#000, .3);
        }
    }

    &__btn {
        margin: 23px 0px 23px 0px;

        @include padding-top(16px);
        @include padding-bottom(16px);
        @include padding-left(120px);
        @include padding-right(120px);

        border-radius: 9px;
        background-color: #A9F2FF;

        font-size: 23px;

        &:hover {
            background-color: #5fd0e4;
        }
    }

    &__policy {
        color: rgba(#fff, .5);
        &:hover {
            text-decoration: underline;
        }
    }
}

//intlTelInput

.iti__country-list {
    max-height: 207px;
    width: 304px;
    color: #000;
    white-space: unset;
    border: none;
    top: 120%;
    border-radius: 8px;

    &::-webkit-scrollbar {
        scrollbar-width: none;
        width: 0;
        height: 0;
    }
}

.iti__selected-flag {
    flex-direction: row-reverse;
}

.iti__flag {
    background-image: url("../images/curriculum/flags.png");
    border-radius: 1px;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
.iti__flag {background-image: url("../images/curriculum/flags@2x.png");}
}

.iti__arrow {
    margin: 0px 16px 0px 0px;
    background-image: url("../images/curriculum/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    width: 14px;
    height: 100%;
    border: none;
}

.iti__arrow--up {
    transform: rotate(180deg);
}

.iti__selected-flag {
    padding: 0px 10px 0px 16px;
}

.iti--allow-dropdown input, .iti--allow-dropdown input[type=tel], .iti--allow-dropdown input[type=text], .iti--separate-dial-code input, .iti--separate-dial-code input[type=tel], .iti--separate-dial-code input[type=text] {
    padding-left: 80px;
}